import useSWR from 'swr'
import axios from "axios";
const apiUrl = 'https://api.syrysapp.com'

const fetcher = (resource, init) => {
    return axios.get(resource, init).then(res => res.data)
}

export function mySwrRequest( url ){
    console.log( "swr request to " + url );
    return useSWR( apiUrl + url, fetcher );
};

